@use './variables' as v;
h1, h2, h3 {
    margin: 0 0 v.$gap 0;
    text-transform: uppercase;
    text-align: center;
    line-height: 1.5;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
    color: v.$primario;
    span {
        color: v.$secundario;
        font-size: 1.8rem;
        display: block;
    }
    &::after {
        content: '';
        display: block;
        width: 10rem;
        height: 10rem;
        margin: 0 auto;
        background-image: url(../img/cafe.svg);
        background-size: cover;
        background-repeat: no-repeat;
    }
}
.heading-blanco {
    @extend h2;
    &,
    span {
        color: v.$blanco;
    }
    &::after {
        display: none;
    }
}
h3 {
    font-size: 3.2rem;
}
p {
    color: v.$negro;
    font-size: 1.6rem;
    line-height: 2;
}