@use '../base/mixins' as m;
@use '../base/variables' as v;

.iconos {
    margin-top: 5rem;

    .listado-iconos {
         @include m.tablet {
            @include m.grid(3, 1rem);
         }

        li {
            img {
                margin: 0 auto;
                width: 12rem;
            }
            p {
                font-family: v.$fuente_cursiva;
                font-size: 3.2rem;
                color: v.$primario;
            }
        }
    }
}
